import { r as redirect } from "../../../chunks/index2.js";
const load = async ({ locals, url }) => {
  if (!locals.session) {
    throw redirect(302, "/auth/login");
  }
  const user = await prisma.user.findUnique({
    where: {
      id: locals.session?.user?.id
    },
    select: {
      id: true,
      name: true,
      email: true,
      image: true,
      password: true
    }
  });
  return { user };
};
export {
  load
};
